<script>
export default {
  components: {
    warningCountIcon: () => import('@/components/warningCount')
  },
  props: ['player'],
  data() {
    return {
      isShowDetail: false,
      socket: this.$store.getters['Game/GetSocket']
    }
  },
  methods: {
    InkrementWarninig ({ nickname = '', id }) {
      this.$dialog.confirm({ text: `Ցանկանու՞մ եք նկատողություն տալ ${nickname}-ին` })
      .then(res => {
        if (res) {
          console.log(id)
          this.socket.emit('inkrementWarning', id)
          this.$dialog.alert({ text: 'ssss' })
        }
      })
    },
    LeaveRoom ({ nickname = '', id }) {
      this.$dialog.confirm({ text: `Ցանկանու՞մ եք նկատողություն տալ ${nickname}-ին` })
      .then(res => {
        if (res) {
          console.log(id)
          this.$dialog.alert({ text: 's' })
        }
      })
    },
    ShowDetail () {
      this.isShowDetail = !this.isShowDetail
    }
  }
}
</script>

<style lang="scss" scoped>
$border-color: #fff;
$bg-color: #f0b128;

svg {
  width: 100%;
  height: 100%;

  path, ellipse {
    fill: $border-color;
  }
}
.st0{stroke:$bg-color;stroke-width:15;stroke-linecap:round;stroke-miterlimit:10;}
.st1{fill:none;stroke:$bg-color;stroke-width:5;stroke-linecap:round;stroke-miterlimit:10;}
.st2{fill:none;stroke:$bg-color;stroke-width:5;stroke-linecap:round;stroke-miterlimit:10;}
.st3{fill:none;stroke:$bg-color;stroke-width:11;stroke-miterlimit:10;}
.st4{stroke:$bg-color;stroke-width:8;stroke-linecap:round;stroke-miterlimit:10;}

.player-list__item {
  width: 100%;
  height: 48px;
  padding: 8px;
  border: 1px solid #cacaca;
  border-radius: 4px;
  overflow: hidden;
  transition: 0.4s ease;

  &.show {
    height: 108px;
    // height: auto;
  }
  &:not(:last-child) {
    margin-bottom: 20px;
  }
  .player-list__title {
    flex-basis: calc(100% - 72px);
    font-size: 20px;
  }
  .warning-count {
    position: relative;
    flex-basis: 50px;
    height: 30px;
    padding: 2px;
  }
  .show-detail {
    flex-basis: 22px;
    height: 22px;
    padding: 5px;

    svg {
      width: 100%;
      height: 100%;
      path {
        fill: #000;
      }
    }
  }
  .player-list__buttons {
    width: 100%;
    height: 50px;
    margin-top: 10px;

    .show-role {
      flex-basis: 50%;
      
      .card {
        width: 40px;
        height: 100%;
        padding-top: 6px;
        border-radius: 4px;
        font-weight: bold;
        font-size: 18px;
        color: #fff;
      }
    }
    .player-list__button {
      flex-basis: 25%;
      margin: 0 6px;
      padding: 8px 0;
      border-width: 2px;
      border-style: solid;
      border-radius: 4px;

      &.warning {
        border-color: #cc941d;
        background-color: $bg-color;
      }
      &.leave {
        border-color: #bd4141;
        background-color: #f15757;
      }
    }
  }
}
</style>